import { useRef, useState, useContext } from 'react';

import {
  Avatar,
  Box,
  Button,
  Divider,
  Hidden,
  lighten,
  Popover,
  Typography,
  useMediaQuery
} from '@mui/material';

import { styled, useTheme } from '@mui/material/styles';
import ExpandMoreTwoToneIcon from '@mui/icons-material/ExpandMoreTwoTone';
import LockOpenTwoToneIcon from '@mui/icons-material/LockOpenTwoTone';
import { AppContext } from 'src/context/AppContext';
// import { User } from 'src/models/user';

const UserBoxButton = styled(Button)(
  ({ theme }) => `
        padding-left: ${theme.spacing(1)};
        padding-right: ${theme.spacing(1)};
`
);

const MenuUserBox = styled(Box)(
  ({ theme }) => `
        background: ${theme.colors.alpha.black[5]};
        padding: ${theme.spacing(2)};
`
);

const UserBoxText = styled(Box)(
  ({ theme }) => `
        text-align: left;
        padding-left: ${theme.spacing(1)};
`
);

const UserBoxLabel = styled(Typography)(
  ({ theme }) => `
        font-weight: ${theme.typography.fontWeightBold};
        color: ${theme.palette.secondary.main};
        display: block;
`
);

const UserBoxDescription = styled(Typography)(
  ({ theme }) => `
        color: ${lighten(theme.palette.secondary.main, 0.5)}
`
);

function HeaderUserbox() {
  const theme = useTheme();

  const { selectedRow } = useContext(AppContext);
  const matches = useMediaQuery(() => theme.breakpoints.down('sm'));

  const user: any = selectedRow;

  if (!user) return null;
  const ref = useRef<any>(null);
  const [isOpen, setOpen] = useState<boolean>(false);

  const handleOpen = (): void => {
    setOpen(true);
  };

  const handleClose = (): void => {
    setOpen(false);
  };

  return (
    <Box sx={{ flex: '1', display: 'flex', alignItems: 'center' }}>
      <Box sx={{ flex: '1', alignItems: 'center', textAlign: 'center' }}>
        {user?.role === 'user' && (
          <Typography variant={matches ? 'h5' : 'h3'}>
            Επόμενο ραντεβού {user.nextApoitment}
          </Typography>
        )}
      </Box>

      <UserBoxButton color="secondary" ref={ref} onClick={handleOpen}>
        <Avatar variant="rounded" alt={user.firstName} />
        <Hidden mdDown>
          <UserBoxText>
            <UserBoxLabel variant="body1">{user.firstName}</UserBoxLabel>
          </UserBoxText>
          <UserBoxText>
            <UserBoxLabel variant="body2">{user.lastName}</UserBoxLabel>
          </UserBoxText>
        </Hidden>
        <Hidden smDown>
          <ExpandMoreTwoToneIcon sx={{ ml: 1 }} />
        </Hidden>
      </UserBoxButton>
      {/* <Popover
        anchorEl={ref.current}
        onClose={handleClose}
        open={isOpen}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right'
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right'
        }}
      > */}
      {/* <MenuUserBox sx={{ minWidth: 210 }} display="flex">
        <Avatar variant="rounded" alt={user.name} />
        <UserBoxText>
          <UserBoxLabel variant="body1">{user.name}</UserBoxLabel>
          <UserBoxLabel variant="body2">{user.surname}</UserBoxLabel>
        </UserBoxText>
      </MenuUserBox> */}
      {/* <Divider sx={{ mb: 0 }} /> */}
      {/* <List sx={{ p: 1 }} component="nav">
          <ListItem button to="/management/profile/details" component={NavLink}>
            <AccountBoxTwoToneIcon fontSize="small" />
            <ListItemText primary="My Profile" />
          </ListItem>
          <ListItem button to="/dashboards/messenger" component={NavLink}>
            <InboxTwoToneIcon fontSize="small" />
            <ListItemText primary="Messenger" />
          </ListItem>
          <ListItem
            button
            to="/management/profile/settings"
            component={NavLink}
          >
            <AccountTreeTwoToneIcon fontSize="small" />
            <ListItemText primary="Account Settings" />
          </ListItem>
        </List> */}
      {/* <Divider /> */}
      {/* <Box sx={{ m: 1 }}>
          <Button color="primary" fullWidth>
            <LockOpenTwoToneIcon sx={{ mr: 1 }} />
            Sign out
          </Button>
        </Box> */}
      {/* </Popover> */}
    </Box>
  );
}

export default HeaderUserbox;
